import Vue from 'vue'

export default {
	data() {
		return {
			userStaff: [],
			page: 1,
			totalPage: null,
		};
	},
	onLoad() {
		this.GetCollectList()
	},
	onReachBottom() {
		if(this.page < this.totalPage){
			this.page += this.page
			this.GetCollectList()
		}
	},
	methods:{
		SendGift(){
			uni.showToast({
				icon: 'none',
				title: '请等待版本更新'
			})
		},
		viewHeadImg(img){
			uni.previewImage({
				urls: [img]
			})
		},
		Delete(index){
			var that = this
			console.log(that.userStaff[index]);
			uni.showModal({
				content: '确定不再关注' + that.userStaff[index].staffInfo.name + '吗？',
				success(res) {
					if(res.confirm){
						uni.request({
							method: "POST",
							header: {
								token: that.Token
							},
							url: that.Server + 'userStaff/del',
							data: {
								staffId: that.userStaff[index].staffInfo.id
							},
							success(res) {
								
								if(res.data.code == 0){
									that.userStaff.splice(index, 1)
								} else if (res.data.code == 101) {
									uni.showToast({
										title: '登录超时，将重新授权登录',
										duration: 500
									})
									setTimeout(() => {
										uni.reLaunch({
											url: '../login/login'
										})
									}, 500)
								} else {
									uni.showModal({
										content: res.data.msg
									})
								}
							}
						})
						
						
					}
				}
			})
		},
		changeMerchant(index){
			var that = this
			var merchantId = that.userStaff[index].staffInfo.merchant_id
			uni.reLaunch({
				url: '../blank/blank?scene=' + merchantId
			})
		},
		orderStaff(index){
			var staffId = this.userStaff[index].staffInfo.id == undefined ? undefined : this.userStaff[index].staffInfo.id
			if(staffId == undefined){
				uni.showToast({
					icon: 'none',
					title: '技师已下岗'
				})
			} else {
				uni.navigateTo({
					url: '../people-detail/people-detail?id=' + staffId
				})
			}
			
		},
		GetCollectList(){
			var that = this
			uni.showLoading({
				title: '请稍等'
			})
			var that = this
			uni.request({
				header: {
					token: that.Token
				},
				url: that.Server + 'userStaff/list',
				data: {
					page: that.page,
					size: 10
				},
				success(res) {
					if(res.data.code == 0){
						that.totalPage = res.data.data.totalPage
						for(var i in res.data.data.list){
							that.userStaff.push(res.data.data.list[i])
						}
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: "none",
							title: '登录超时，将重新授权登录',
							duration: 1000
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 2000)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 1000
						})
					}
					uni.hideLoading()
				}
			})
		},
		book(e){
			this.modalName = null
			// uni.navigateTo({
			// 	url: "../people-detail/people-detail?status=" + e.currentTarget.dataset.key
			// })
		},
	}
}
